import { Grid } from '@material-ui/core';
import { useSelector } from 'react-redux';
import _get from 'lodash.get';
import { Settings } from './Settings';
import Loader from '../../../../../ui-component/Loader';
import useError from './hooks/useError';
import useConfig from './hooks/useConfig';
import useAppointments from './hooks/useAppointments';
import Scheduler from './Scheduler';
import { TProperty } from '../../../../../types';

type Props = {
	property: TProperty;
};

const Calendar = ({ property }: Props) => {
	const isLoading = useSelector(state => _get(state, ['calendar', 'isLoading']));
	const [triggerSaveSettings] = useConfig(property);
	const profileId = _get(property, 'HomeViewingCalendarProfileId');
	useAppointments({ profileId });
	useError();

	return (
		<>
			{isLoading && <Loader />}
			<Grid container spacing={1} justifyContent='center'>
				<Grid item xs={12} md={12} lg={12}>
					<Settings triggerSaveSettings={triggerSaveSettings} />
				</Grid>
				<Grid item xs={12} md={12} lg={12}>
					<Scheduler profileId={profileId} />
				</Grid>
			</Grid>
		</>
	);
};

export default Calendar;
